/**
 * [cutoutImgMain ]
 * @param  {[type]} imgSrc   []
 * @param  {[type]} imgData  [ left top width height]
 * @param  {[type]} fileType [base64]
 * @return {[type]}          [base64]
 */
export function cutoutImgMain(imgSrc, imgData, fileType) {
   return new Promise((resolve, reject) => {         
        let spr_val = 10, //
            sX = imgData.left - spr_val, //left
            sY = imgData.top - spr_val, //top
            sWid = imgData.width + spr_val*2, //
            sHei =imgData.height + spr_val*2; //
            
        let image = new Image();
        image.setAttribute("crossOrigin", "anonymous");
        image.src = '/uploadFiles/images/' + imgSrc;

        image.onload = function() {
            let canvas = document.createElement("canvas");
        
            let context = canvas.getContext("2d");
            canvas.width = sWid;
            canvas.height = sHei;
            context.drawImage(image, sX, sY, sWid, sHei,0,0,sWid,sHei);
            let path_cut = '';
            if(fileType == 'png') {
                path_cut = canvas.toDataURL("image/png")
            }else if(fileType == 'jpg') {
                path_cut = canvas.toDataURL("image/jpeg")
            }else{
                path_cut = canvas.toDataURL("image/png")
            }
            
            // console.log('path_cut',path_cut)
            
            if(path_cut != ''){
                resolve(path_cut);
            }else{
                reject('')
            }
        };    
    })
}

